@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
@media only screen and (max-width: 480px) {
  car {
    width: 100%;
    height: auto;
  }
}

.active {
  font-size: 18px;
  text-align: center;
}

.car {
  width: 200px;
  height: auto;
}

.tech {
  color: #f1356d;
}
.tech :hover {
  color: #f2f2f2;
}
/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #f2f2f2;
  background-color: #2b2b2b;
}
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 100%;
  margin: 0 auto;
  border-bottom: 1px solid #f1356d;
}

@media screen and (max-width: 500px) {
  .links a {
    float: none;
    display: block;
    text-align: left;
  }
  .newblog {
    float: left;
  }
}

.navbar h1 {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #f1356d;
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #2b2b2b;
}

.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}

.blog-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

h {
  align-content: center;
}

.Maths,
.electric,
.AI,
.Social-media,
.blockchain,
.Technology {
  padding: 10px;
  border: solid lightblue;
  margin-top: 20px;
  border-radius: 10px;
}

.read-more-read-less-buton {
  border-radius: 5px;
  background-color: #f1356d;
}

.footer {
  color: red;
}

.navbar {
  -webkit-animation: bounce-top 0.9s both;
  animation: bounce-top 0.9s both;
}

.electric :hover {
  -webkit-animation: flip-2-hor-top-2 0.5s
    cubic-bezier(0.455, 0.03, 0.515, 0.955) both;
  animation: flip-2-hor-top-2 0.5s cubic-bezier(0.455, 0.03, 0.515, 0.955) both;
}

.button1 {
  background-color: #06516e;
  color: #ffffff;
  width: 50px;
  height: 50px;
}

.button2 {
  background-color: #0ecc2794;
  color: #ffffff;
  width: 100px;
  height: 50px;
}

.button2:hover {
  background-color: #03ac2294;
}
.dropdown {
  position: relative;
}

.dropdown-content {
  position: absolute;
  display: none;
}

.dropdown:hover .dropdown-content {
  display: block;
  z-index: 10;
}

.button {
  cursor: pointer;
}

.container pt-5, .submit{
  align-self:center;
}

.submit{
  margin: 10px;
}
